@import url(http://fonts.cdnfonts.com/css/breathing-personal-use);
body {
  margin: 0;
  /* font-family: "Times New Roman", Times, serif; */
  font-family: Montserrat, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Breathing Personal Use";
  src: local("Breathing Personal Use"),
   url(/weather-app/static/media/breathingpersonaluse-mvogj.f6f7391f.ttf) format("truetype");
  font-weight: bold;
}
* {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  width: 100%;
  background-image: url(/weather-app/static/media/background_image.83c0ab8f.jpg);
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.App .title {
  color: white;
  font-size: 4rem;
  font-weight: 400;
  margin-top: 15%;
  font-style:bold;
  font-family: 'Breathing Personal Use', sans-serif;
}

@media (max-width: 1200px) {
  .App .title {
    font-size: 3rem;
  }
}

@media (max-width: 1000px) {
  .App .title {
    font-size: 2.5rem;
  }
}

@media (max-width: 800px) {
  .App .title {
    font-size: 2rem;
  }
}

.input-div,
.input-div * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.input {
  width: 90%;
  height: 3rem;
  border: none;
  border-radius: 0 30px 30px 0;
  font-size: 1.2rem;
  outline: none;
}

.input-div {
  position: relative;
  height: 3rem;
  box-sizing: border-box;
  padding: 0;
  margin: 5% auto;
  width: 50%;
  border-radius: 30px;
  background-color: white;
  opacity: 80%;
}

.input-div i {
  width: 10%;
  font-size: 1.5rem;
  opacity: 50%;
}

@media (max-width: 1300px) {
  .input-div {
    width: 60%;
  }
}

@media (max-width: 1100px) {
  .input-div {
    width: 70%;
  }
}

@media (max-width: 1000px) {
  .input-div {
    width: 80%;
  }
}

@media (max-width: 800px) {
  .input-div {
    width: 90%;
  }

  .input-div i {
    font-size: 1rem;
  }
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.current-weather-div {
  /* background-color: pink; */
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.main,
.secondary {
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
}

.main {
  position: relative;
  color: white;
}

.place_time {
  padding: 30px;
  text-align: left;
}

.place_time .place {
  line-height: 1.5;
  font-size: 3.5rem;
}

.place_time .time {
  font-size: 2rem;
}

.weather {
  position: relative;
}

.weather .icon {
  position: relative;
  left: -10%;
  width: 35%;
  height: 35%;
}

.weather .temp {
  position: absolute;
  top: 25%;
  left: 55%;
  font-size: 5rem;
}

.weather .desc {
  position: absolute;
  font-size: 2rem;
  font-weight: 400;
  left: 55%;
  top: 65%;
  text-transform: capitalize;
}

@media (max-width: 1200px) {
  .place_time .place {
    font-size: 3rem;
  }

  .place_time .time {
    font-size: 1.8rem;
  }

  .weather .temp {
    font-size: 4rem;
  }

  .weather .desc {
    font-size: 1.8rem;
  }
}

@media (max-width: 1000px) {
  .place_time .place {
    font-size: 2.5rem;
  }

  .place_time .time {
    font-size: 1.3rem;
  }

  .weather .temp {
    font-size: 3.5rem;
  }

  .weather .desc {
    font-size: 1.3rem;
  }
}

@media (max-width: 800px) {
  .current-weather-div {
    display: block;
  }

  .secondary {
    margin-left: 2%;
  }

  .place_time .place {
    font-size: 2rem;
  }

  .place_time .time {
    font-size: 1rem;
  }

  .weather .temp {
    font-size: 3rem;
  }

  .weather .desc {
    font-size: 1rem;
    top: 70%;
  }
}

.secondary-list {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  width: 98%;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.secondary-list-item {
  -webkit-flex-basis: 32%;
          flex-basis: 32%;
  /* background-color: red; */
  margin: 3px;
  height: 6rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
}

.secondary-list-item h1 {
  font-size: 2.3rem;
  font-weight: 600;
}

.secondary-list-item h3 {
  font-size: 1.5rem;
  font-weight: 400;
}

@media (max-width: 1300px) {
  .secondary-list-item h1 {
    font-size: 2rem;
  }

  .secondary-list-item h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 1100px) {
  .secondary-list-item {
    -webkit-flex-basis: 30%;
            flex-basis: 30%;
  }

  .secondary-list-item h1 {
    font-size: 1.5rem;
  }

  .secondary-list-item h3 {
    font-size: 1rem;
  }
}

.forecast-weather {
  box-sizing: border-box;
  padding: 0;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  overflow-x: scroll;
  min-height: 200px;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 1%;
  margin-bottom: 10%;
}

.forecast .title {
  text-align: left;
  text-transform: capitalize;
  font-size: 2.2rem;
  padding-left: 30px;
}

.forecast {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  margin-top: -10%;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: 500px) {
  .forecast-weather {
    margin-bottom: 15%;
  }
}

@media (max-width: 300px) {
  .forecast-weather {
    margin-bottom: 20%;
  }
}

.forecast-weather-hour,
.forecast-weather-hour,
.forecast-weather-hour * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.forecast-weather-hour {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  margin: 0 0.5%;
  border-radius: 10px;
  color: white;
  min-width: 150px;
  height: 160px;
}

.forecast-weather-hour .date,
.forecast-weather-hour .month {
  font-size: 1.2rem;
  font-weight: 500;
}

.forecast-weather-hour .date {
  margin-top: 5%;
}

.forecast-weather-hour .temp {
  font-size: 2rem;
}

.forecast-weather-hour .icon {
  width: 50px;
  height: 50px;
}

.error {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  border-radius: 10px;
  width: 40%;
  margin: 0 auto;
  height: 6rem;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 0.7rem;
  font-weight: 100;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (max-width: 1200px) {
  .error {
    width: 60%;
  }
}

@media (max-width: 800px) {
  .error {
    width: 80%;
  }
}

.footer {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  width: 100%;
  text-align: center;
  height: 4rem;
  margin-top: 2%;
  padding-top: 1%;
  padding-left: 3%;
  position: absolute;
  bottom: 0;
  box-shadow: none;
  color: Beige;
  font-family: 'Breathing Personal Use', sans-serif;
}

.footer h1 {
  font-weight: 400;
  font-size: 1.5rem;
}

.footer b {
  font-family: 'Breathing Personal Use', sans-serif;
}

@media (max-width: 700px) {
  .footer h1 {
    font-size: 1rem;
  }

  .footer {
    height: 3rem;
  }
}

